import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '../components/login.vue'
import reg from '../components/reg.vue'
import bar_detail from '../components/bar_detail.vue'
import bookdetail from '../components/bookdetail.vue'
import tabbar from '../views/tabbar.vue'
import booknext from '../components/booknext.vue'
import books from '../components/books.vue'
import readingdetail from '../components/readingdetail.vue'
import bookcart from '../components/bookcart.vue'
// import me from '../components/me.vue'
//引入子组件


Vue.use(VueRouter)
//注册子组件
const routes = [
 
  // {
  //   path: '/about',
  //   name: 'about',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // },
  // {
  //   path:"/me",
  //   component:me
  // },
  {
    path:"/login",
    component:login
  },
  {
    path:'/reg',
    component:reg
  },

  {
    path:"/bookdetail",
    component:bookdetail
  },
  {
    path:"/bar_detail",
    component:bar_detail,
    beforeEnter:(to,from,next)=>{
      if(sessionStorage.getItem('username')){
        next()
      }else{
        alert('请登录')
        next('/')
      }
    }
  },
  {
    path:"/",
    component:tabbar
  },
  {
    path:"/booknext",
    component:booknext,
    beforeEnter:(to,from,next)=>{
      if(sessionStorage.getItem('username')){
        next()
      }else{
        alert('请登录')
        next('/')
      }
    }
    
  },
  {
    path:'/books',
    component:books
  },
  {
    path:'/readingdetail',
    component:readingdetail
  },
  {
    path:"/cart",
    component:bookcart,
    beforeEnter:(to,from,next)=>{
      if(sessionStorage.getItem('username')){
        next()
      }else{
        alert('请登录')
        next('/')
      }
    }
  }
  
]

const router = new VueRouter({
  routes,
  mode:"history"
})

export default router
