<template>
    <main>
        <mt-button style="width:100%">购物车列表</mt-button>
        <div class="top">
            <input type="checkbox" v-model="allchoose" @change="ch">全选
            购物车数量：{{this.$store.state.cartcount}}
        </div>

        <div class="flex container" v-for="(t,i) of cart" :key="i" >
            <div class='height flex'>
                <input type="checkbox" v-model="t.choose" @change="single">
            </div>
            <div class="flex w height">
                    <img :src="t.img" alt="">
                    <span>
                        {{t.title}}
                    </span>
            </div>
            <div class="height height flex">
                <span>
                单价:{{t.price}}</span>
            </div>
            <div class="height flex">
                <button @click="changeprice(-1,i)">-</button>
                <span>{{t.count}}</span>
                <button @click="changeprice(+1,i)">+</button>
            </div>
            
            <div class="height flex">
                <span>
                合计：{{Math.round(parseFloat(t.price*t.count)*100)/100}}
                </span>
            </div>
            <div :data-cid="t.cid" @click="del" class="height flex">
                <span>
                删除
                </span>
            </div>
        </div>

        <div class="alltotal">
                <mt-button>总价{{Math.round(parseFloat(alltotals)*100)/100}}</mt-button>
                
                <mt-button type="primary">结算</mt-button>
        </div>

        <div class="flex">
            <mt-button @click="delm" type="danger">选中删除</mt-button>
            <mt-button @click="clear" icon="more" type="primary">清空购物车</mt-button>
        </div>
       
       
    </main>
</template>

<style scoped>
    .height>button{
        width:20px;
        height:20px
    }
    .flex{
        display:flex;
        justify-content: space-between;
        flex-wrap: nowrap !important}
    img{
        width:50px !important;
        height:65px}
    .container{
        height:70px;
        margin:10px 0;
        border:1px solid #f5f5f5}
    .height{
        align-items: center;
        }
    div{
        font-size:12px}
   .w{
       width:80px;
       font-size: 12px}
   .top{
       text-align: start;
       padding:20px
       }
   .alltotal span{
       background: rgb(211, 70, 15);
       color:#f8f8f8;
       width:55px;
       height:25px;
       border-radius: 10px;
       padding:5px;
       display:inline-block;
       text-align: center !important;
   }
   .alltotal span:active{
       border:1px solid red}
   .alltotal{
       position: absolute;
       right:30px;
       bottom:40px;
       border:1px solid #f5f5f5;
       width:80%;
       text-align: end;
       border-radius: 10px}
</style>
<script>
    export default{
        data(){
            return{
                //全选按钮的状态
                allchoose:false,
                //保存购物车的所有商品
                cart:[],
                alltotals:0,
            }
        },
        methods:{
            //所有商品的总价
            alltotal(){
                var alltotal=0;
                for(var item of this.cart){
                    if(item.choose===true){
                    alltotal+=item.price*item.count
                    }
                }
                return alltotal;
            },
            //添加减少商品数量
            changeprice(num,i){
                this.cart[i].count+=num;
                if(this.cart[i].count<0){
                    this.cart[i].count=0
                }
                var num=0;
                for(var item of this.cart){
                    num+=parseInt(item.count)
                }
                this.$store.state.cartcount=num;
                this.alltotals=this.alltotal();
            },
            //删除单个商品
            del(e){
                var cid=e.target.dataset.cid;
                console.log(cid)
                this.$messagebox.confirm("是否删除该商品")
                .then(res=>{
                    this.axios.get("delcart",{params:cid})
                    .then(res=>{
                        this.getcart()
                    })
                    })
                .catch(err=>{console.log(err)})
                
            },
            //删除多个商品
            delm(){
                var str="";
                for(var item of this.cart){
                    if(item.choose==true){
                        str+=item.id+","
                    }
                }
                if(str.length==0){
                    this.$toast("选择要删除的商品")
                    return;
                }
                str=str.substring(0,str.length-1);
                console.log(str);
                this.$messagebox.confirm("是否删除这些商品")
                .then(res=>{
                    var obj={
                        ids:str
                    }
                    this.axios.get("delm",{params:obj})
                    .then(res=>{console.log(res)
                    this.getcart()
                    })
                    .catch(err=>{console.log(err)})
                })
                .catch(err=>{})
            },
            //清空购物车
            clear(){
                if(this.cart.length===0){
                    this.$toast("没有商品")
                }
                this.$messagebox.confirm("是否清空购物车")
                .then(res=>{
                this.$store.commit("clearcart");
                this.axios.get("clearcart")
                .then(res=>{
                    this.getcart()
                })
                .catch(err=>{console.log(err)})
                })
                .catch(err=>{console.log(err)})
            },
            //获取购物车商品
            getcart(){
             this.axios.get("getcart")
            .then(res=>{
                this.alltotals=this.alltotal();
                var allcart=res.data.data
                this.cart=allcart;
                var num=0;
                for(var item of this.cart){
                    
                    num+=parseInt(item.count)
                   
                }
                this.$store.state.cartcount=num;

            })
            .catch(err=>{console.log(err)})
            },
            //全选选中
            ch(){
                for(var item of this.cart){
                    item.choose=this.allchoose;
                }
            },
            //
            single(){
                var a=0;
                for(var item of this.cart){
                    if(item.choose===true){
                        a++;
                    }
                }
                if(a===this.cart.length){
                    this.allchoose=true
                }
                else{
                    this.allchoose=false
                }
            }
        },
        created(){
            this.getcart();
            console.log(this.cart)
        },
        updated(){
            this.alltotals=this.alltotal()
        }
    }
</script>