<template>
    <main>
        <div class="container">
                <el-button type="success" class="el">书柜</el-button>
            <div class="child1">
                <img src="../assets/book01.png" alt="">
            </div>
            <div class="child2">
                <img src="../assets/phone01.png" alt="">
                <!-- <input name="" id="in1" placeholder="用户名" v-model="value1" @blur="user"> -->
                <input type="text" placeholder="用户名" @focus="show" data-layout="normal" @blur="user"/>
                <vue-touch-keyboard :options="options" v-if="visible" :layout="layout" :cancel="hide" :accept="accept" :input="input" />
               
            </div>
            <div class="h" id="d1"></div>
            <div class="child2">
                <img src="../assets/key01.png" alt="">
                <!-- <input name="" id="in2" placeholder="密码" v-model="value2" @blur="upwd"> -->
                <input type="text" placeholder="密码" @focus="show2" data-layout="normal" @blur="upwd"/>
                
                <vue-touch-keyboard :options="options" v-if="visible2" :layout="layout2" :cancel="hide2" :accept="accept2" :input="input2" />
                
            </div>
            <div class="h" id="d2"></div>
            <button class="btn" @click="login">登录</button>
            <div class="bottom">
                <div @click="goreg">新来的<a href="javascript:;">创建一个账户</a></div>
                <div>忘记密码</div>
            </div>
            
            <div class="center">-或-</div>
             <div class="last">
                <img src="../assets/weixin01.png" alt="">
                <p>微信登录</p>
             </div>
        </div>
        <div class="fix" @click="me">×</div>
    </main>
</template>
<style scoped>
 
    .el{
        width:100% !important
        }
    .container{
        width:80%;
        margin:20px auto
        }
    .child1{
        text-align:center;
        margin-bottom:30px
        }
    .child2{
        width:100%;
        border-bottom:1px solid #eee;
        text-align:start
        }
    .child2>img{
        width:20px;
        height:20px;
        vertical-align:bottom
        }
    .child2>input{
        height:20px;
        width:90%;
        }
    .bottom{
        display:flex;
        justify-content:space-between}
    a{
        text-decoration:none
        }
    .center{
        text-align:center}
    .h{
        height:20px}
    .fix{
        position:fixed;
        top:10px;right:10px
        }
    .btn{
        background:green;
        width:100%;
        height:35px;
        border:0;
        margin:30px 0
        }
    .center{margin:30px 0}
    p{margin:0}
</style>
<script>
import qs from 'qs'
    export default{
        data(){
            return{
                value1:"",
                value2:"",
                visible: false,
                layout: "normal",
                input: null,
                options: {
                    useKbEvents: false,
                    preventClickEvent: false
                },
                 visible2: false,
                layout2: "normal",
                input2: null,
            }
        },
        methods:{
            accept(text) {
            this.value1=text
            this.hide();
            this.user()
            },
    
            show(e) {
            this.input = e.target;
            this.layout = e.target.dataset.layout;
            this.visible2=false
            if (!this.visible)
                this.visible = true
            },
    
            hide() {
            this.visible = false;
            },
            //
            accept2(text) {
            this.value2=text
            this.hide2();
            this.upwd()
            },
    
            show2(e) {
            this.input2 = e.target;
            this.layout2 = e.target.dataset.layout;
            this.visible=false
            if (!this.visible2)
                this.visible2 = true
            },
    
            hide2() {
            this.visible2 = false;
            },
            //登录
            login(){
                var v1=this.value1
                var v2=this.value2
                console.log(v1,v2)
                var obj={
                    uname:v1,
                    upwd:v2
                }
                this.axios.post("login",{headers:{'Content-Type':'application/x-www-form-urlencoded'}},{params:obj})
                .then(res=>{
                    if(res.data.code==-2){
                        alert("用户名或者密码错误")
                    }else{
                        alert("登录成功")
                        console.log(res.data.data)
                        this.$store.state.user=res.data.data;
                        sessionStorage.setItem('username',res.data.data.split('迎')[1])
                        this.$router.push("/")
                    }
                })
                .catch(err=>{
                    console.log(err)
                })
            },
            //验证账户是否合理
            user(){
                var reg=/^([A-Za-z0-9]|[._]){3,13}$/
                var v1=reg.test(this.value1)
                
                var d1=document.getElementById("d1")
                
                if(!v1){
                    d1.innerHTML="用户名格式有误"
                    d1.style.color="red"
                }else{
                    d1.innerHTML="用户名格式正确"
                    d1.style.color="green"
                }
            },
            //验证密码是否合理
            upwd(){
                var reg=/^([A-Za-z0-9]|[._]){6,13}$/
               
                var v2=reg.test(this.value2)
                
                var d2=document.getElementById("d2")
                if(!v2){
                    d2.innerHTML="密码格式有误"
                    d2.style.color="red"
                }else{
                    d2.innerHTML="密码格式正确"
                    d2.style.color="green"
                }
            },
            //注册页面
            goreg(){
                this.$router.push("/reg")
            },
            me(){
                this.$router.push("/")
            }
        },
        created(){

        },
        mounted(){
            window.onresize=()=>{

            }
        }
    }
</script>