<template>
    <main>
        <div class="flex top">
            <mt-button icon="back" @click="back" class="mt"></mt-button>
            <!-- <img src="../assets/返回.png" alt="" @click="back"> -->
            <div>书籍详情</div>
            <img src="" alt="">
        </div>
         <div :style="'background-image:url('+booklist[0].img+')'" class="coll" v-if="booklist.length>0"></div>
         
        <div class="flex col" v-if="booklist.length>0">
           
            <div class="title">
                <h5></h5>
                <p>作者：{{booklist[0].auther}}</p>
                <div class="block">
                    <span class="demonstration"></span>
                    <el-rate v-model="booklist[0].grade"></el-rate>
                </div>
                <p>字数：{{booklist[0].fontsum}}</p>
            </div>
            <img :src="booklist[0].img" alt="">
        </div>
        <div class="comment" v-if="booklist.length>0">简介：{{booklist[0].content}}</div>
        <hr>
        <div class="program">目录</div>
        <div class="scr">
            <div class="flex language" v-for="(t,i) of catalogue" :key="i">
                <p>{{t.list}}</p>
                <p @click="bookdetail" :data-title="t.title" :data-content="t.content">{{t.title}}</p>
            </div>
        </div>
        <div class="flex bottom">
            <div>热门评论</div>
            <div @click="show" id="comment">发表评论</div>
        </div>
        <div class="hide re inp">
            <div class="ab" @click="hidden">×</div>
            <textarea name="" id="inp" cols="30" rows="10" v-model='value'></textarea>
            <!-- <input id="inp" type="text" v-model="value"> -->
            <button @click="comments">发表评论</button>
        </div>
        <div class="com">
            <div v-for="(t,i) of list" :key="i" >
                <div>
                    <div>{{t.uname}}</div>
                    <div>{{t.time | ftime}}</div>
                </div>
                <div>{{t.content}}</div>
            </div>
        </div>
        
    </main>
</template>

<style scoped>
    .scr{
        overflow:scroll;
        max-height:150px
    }
    .mt{
        background-color: transparent !important
        }
    .flex{
        display:flex;
        justify-content:space-between
        }
    .top{
        background-image:linear-gradient(mediumaquamarine,rgb(54, 233, 123));height:40px;
        line-height: 40px;
        margin-bottom:5px}
    .top img{
        width:30px;
        height:30px}
    .top h5{
        margin:0
        }
    .title{
        text-align: start;
        font-size: 14px}
    .comment{
        font-size: 14px
        }
    .col{
        position: relative ;
        z-index:2;
        }
    .coll{
        position: absolute;
        filter:blur(50px);
        width:100%;
        height:280px ;
        z-index: 1}
    .colll{
        opacity: 0.9;
        }
    .program{
        background:mediumaquamarine;
        margin:5px 0;
        padding:3px
        }
    .active{
        display:block !important}
    .hide{
        display:none
        }
    .re{
        position:relative
        }
    .ab{
        position:absolute;
        right:0;
        top:0}
    .bottom>div{
        background:mediumaquamarine;
        padding:3px 5px;
        border-radius: 5px;
    }
    .bottom{
        font-size: 14px;
        }
    .bottom>div:last-child:active{
        background:rgb(195, 224, 213)}
    .com{
        background:mintcream
        }
    .com>div{
        border-bottom:1px solid #eee
        }
    .com>div>div:first-child{
        display:flex;
        justify-content: space-between;
        font-size: 18px
        }
    .com>div>div:last-child{
        padding:10px 10px 10px 30px;
        text-align: start
        }
    .language{
        
        background:rgb(219, 168, 117);
        border-radius: 5px;height:30px;
        margin:10px 0;
        padding:0 15px;
        line-height: 30px;
        }
    .language>p{
        margin:0
        }
    #inp{
        width:100%;
        height:50px;
        margin:15px 0}
    .inp{
        border-top:1px solid #f8f8f8
        }
    .inp>button{
        background: rgb(192, 205, 228);
        border-radius: 5px;
        border-width:0;
        box-shadow: 1px 1px 1px 1px rgb(211, 218, 211);
        padding:3px 5px}
</style>
<script>
    export default{
        data(){
            return{
                tid:0,
                //保存用户输入的评论
                value:"",
                //保存评论
                list:[],
                //保存单个导航书籍详情
                booklist:[],
                //保存书籍目录
                catalogue:[]
            }
        },
        methods:{
        
            back(){
                this.$router.push("/")
            },
            //隐藏评论框
            hidden(){
                var hidden=document.getElementsByClassName("ab")
                hidden[0].parentElement.classList.remove("active")
            },
            //显示评论框
            show(){
               var comment=document.getElementById("comment")
               comment.parentElement.nextElementSibling.classList.add("active")
            },
            //目录跳转内容
            bookdetail(e){
                this.$store.commit("changetitle",e.target.dataset.title);
                this.$store.commit("changecontent",e.target.dataset.content)
                this.$router.push("/bookdetail")
            },
            //发表评论
            comments(){
                var time=new Date().getTime();
                var obj={
                    tid:this.tid,
                    content:this.value,
                    time:time
                }
                console.log(obj)
                this.axios.get("comment",{params:obj})
                .then(res=>{
                    console.log(res)
                    if(res.data.code==1){
                        alert("发表成功")
                        this.commentlist(); 
                    }else{
                        alert("发表失败")
                    }
                })
                .catch(err=>{console.log(err)})
            },
           // 获取评论
            commentlist(){
            var obj={
                tid:this.tid
            }
            this.axios.get("list",{params:obj})
            .then(
                res=>{
                    console.log(res);
                    this.list=res.data.data.reverse();
                    console.log(this.list)
                }
            )
            .catch(err=>{console.log(err)})
            },
            
           
        },
        created(){
            //获取上级参数id
            this.tid=this.$route.query.tid;
             //获取评论
            this.commentlist();
            //获取书籍详情
            var obj={
                tid:this.tid
            }
            this.axios.get("getbooks",{params:obj})
            .then(res=>{
               
                this.booklist=res.data.data
            })
            .catch(
                err=>{console.log(err)}
            )
            //获取书籍目录
            this.axios.get("bookdetail",{params:obj})
            .then(res=>{
                
                this.catalogue=res.data.data
                 console.log(this.catalogue)
            })
            .catch(err=>{console.log(err)})
        },
        mounted(){
           
        }
    }
</script>