<template>
    <main>
        <div class="back flex">
            <img :src="books.img" alt="">
            <div>
                <p>{{books.title}}</p>
                <p>作者：{{books.auther}}</p>
                <div class="block">
                    <span class="demonstration"></span>
                    评分：<el-rate v-model="books.grade"></el-rate>
                </div>
                <p>{{books.fontsize}}万字</p>
                <p>￥:{{books.price}}</p>
                <div class="nback">{{books.content}}</div>
            </div>
        </div>
        <div class="middle flex">
            <mt-button type="danger" :data-id="books.id" @click="addbookshelf">
            加入书架</mt-button>
            <mt-button type="primary" :data-id="books.id" @click="addcart">
            加入购物车
            </mt-button>
        </div>
        <div>
            <p>章节目录</p>
            <div class="flex active w" id="program">
                <div v-for="(t,i) of booksdetail" :key="i" class="section" @click="readingdetail" :data-section="t.section">
                    {{t.section}}：{{t.dname}}  
                </div>
            </div>
            <div @click="show" class="bottom">
                <p id="all">展开全部</p>
            </div>
        </div>
        <div class="fixed" @click="prev">
            <img src="../assets/叉叉.png" alt="">
        </div>
    </main>
</template>
<style scoped>
    .fixed{
        position: fixed;
        right:10px;
        top:10px
        }
    .flex{
        display:flex;
        justify-content: space-between;}
    .active{
        height:20px
    }
    .w{
        font-size: 12px;
        text-align: start !important;
        overflow: hidden;
        flex-wrap:wrap !important}
    /* .back{background-image: url(books.img)} */
    .section{
        width:150px;
        height:20px;
        margin:3px 5px;}
    .back{
        text-align: start;
        font-size:14px;}
    .back img{
        width:50%;}
    .back div{
        margin-left:10px}
    .nback{
        background:lightsteelblue;
        border-radius: 5px;
        width:100%;height:115px;
        text-overflow: ellipsis;
        overflow: hidden;
        margin-left:0 !important;
        word-break: break-all;
        display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 6;
    color:#111
    }
    .middle{
        text-align: start;
        margin:10px;
        border-bottom:1px solid #f5f5f5}
    p{
        border-bottom:1px solid #f5f5f5;
        margin:5px
        }
    .bottom p{
        padding:5px !important;
        color:white;
        background:lightsteelblue;
        width:100px
        }
</style>
<script>
import eventBus from '../eventBus.js'
    export default{
        data(){
            return{
                id:0,
                books:{},
                booksdetail:[],
                img:""
            }
        },
        methods:{
            //加入购物车
            addcart(e){
                var id=e.target.dataset.id;
                this.axios.get("addcart",{params:id})
                .then(res=>{
                    alert('添加成功')
                    console.log(res)
                    })
                .catch(err=>{console.log(err)}) 
            },
            prev(){
                this.$router.push("/")
            },
            //显示章节
            readingdetail(e){
                var section=e.target.dataset.section;            
                this.$router.push('/readingdetail?section='+section)
            },
            //加入书架
            addbookshelf(e){
                var id=e.target.dataset.id;
                this.axios.get("addshelf",{params:id})
                .then(
                    res=>{
                        alert('添加成功')
                        console.log(res)
                })
                .catch(err=>{console.log(err)})
            },
            show(){
                var p=document.getElementById("all")
                var program=document.getElementById("program")
                //判断元素有没有这个class属性
                if(program.classList.contains("active")){
                    program.classList.remove("active")
                    p.innerText="收起"
                 }else{
                    program.classList.add("active")
                    p.innerText="展开全部"
                }
               
            },
            //获取详情
            getbooknext(){
                var obj={
                    id:this.id
                }
                this.axios.get("getbooknext",{params:this.id})
                .then(res=>{
                    this.books=res.data.data[0]
                    this.img=res.data.data.img
                    console.log(this.books)
                })
                .catch(err=>{
                    console.log(err)
                })
            },
            //章节
            getbooksdetail(){
                this.axios.get("getbooksdetail",{params:this.id})
                .then(res=>{
                    this.booksdetail=res.data.data
                })
                .catch(err=>{console.log(err)})
            }
        },
        created(){
            this.id=this.$route.query.id;
            this.$store.commit("changeid",this.id)
            this.getbooksdetail()
            this.getbooknext();
        },
        
    }
</script>