<template>
    <main>
        <div class="container">
            <div class="center">注册阅读账号</div>
            
            <div class="input">
                <img src="../assets/phone01.png" alt="">
                <!-- <input name="" placeholder="用户名" v-model="value1" @blur="user"> -->
                <input type="text" placeholder="用户名" @focus="show" data-layout="normal"/>
                <vue-touch-keyboard :options="options" v-if="visible" :layout="layout" :cancel="hide" :accept="accept" :input="input" />    
            </div>
            <div id="d1" class="h"></div>
            <div class="input">
                <img src="../assets/key01.png" alt="">
                <!-- <input name="" placeholder="密码" v-model="value2" @blur="upwd"> -->
                <input type="text" placeholder="密码" @focus="show2" data-layout="normal" />
                <vue-touch-keyboard :options="options" v-if="visible2" :layout="layout2" :cancel="hide2" :accept="accept2" :input="input2" />
            </div>
            <div id="d2" class="h"></div>
            <div class="input"> 
                <img src="../assets/key01.png" alt="">
                <!-- <input name="" placeholder="二次密码" v-model="value3" @blur="pwd"> -->
                <input type="text" placeholder="密码" @focus="show3" data-layout="normal"/>
                <vue-touch-keyboard :options="options" v-if="visible3" :layout="layout2" :cancel="hide3" :accept="accept3" :input="input3" />
            </div>
            <div id="d3" class="h"></div>
            <button @click="reg">注册</button>
            <div class="center">*注册视为同意《用户许可协议》</div>
            <div>-或-</div>
            <div class="center">
                <img src="../assets/weixin01.png" alt="">
                <p>微信登录</p>
            </div>
        <div class="glabel" @click="gologin"><img src="../assets/返回.png" alt=""></div>
        </div>
    </main>
</template>
<style scoped>
    .container{width:80%;
        margin:20px auto}
    .center{text-align:center;
        margin:20px 0}
    button{width:100%;
        height:35px;
        background:green;
        border:0;
        text-align:center}
    .input{width:100%;
        border-bottom:1px solid #eee;
        padding:3px}
    .input>img{width:20px;
        height:20px;
        vertical-align:bottom}
    .input>input{width:80%}
    .h{height:20px}
    p{margin:0}
    .glabel{position:fixed;
        top:10px;
        left:10px}
    
</style>
<script>
    export default{
        data(){
            return{
                value1:"",
                value2:"",
                value3:"",
                visible: false,
                layout: "normal",
                input: null,
                options: {
                    useKbEvents: false,
                    preventClickEvent: false
                },
                visible2: false,
                layout2: "normal",
                input2: null,
                visible3: false,
                layout3: "normal",
                input3: null,
            }
        },
        methods:{
            accept(text) {
            this.value1=text
            this.hide();
            
            },
    
            show(e) {
            this.input = e.target;
            this.layout = e.target.dataset.layout;
            this.visible2=false
            this.visible3=false
            if (!this.visible)
                this.visible = true
            },
    
            hide() {
            this.visible = false;
            },
            accept2(text) {
            this.value2=text
            this.hide2();
            
            },
    
            show2(e) {
            this.input2 = e.target;
            this.layout2 = e.target.dataset.layout;
            this.visible=false
            this.visible3=false
            if (!this.visible2)
                this.visible2 = true
            },
    
            hide2() {
            this.visible2 = false;
            },
            accept3(text) {
            this.value3=text
            this.hide3();
            
            },
    
            show3(e) {
            this.input3 = e.target;
            this.layout3 = e.target.dataset.layout;
            this.visible=false
            this.visible2=false
            if (!this.visible3)
                this.visible3 = true
            },
    
            hide3() {
            this.visible3 = false;
            },
            //注册
            reg(){
                console.log(this.value1)
                console.log(this.value2,this.value3)
                if(this.value2!==this.value3){
                    alert('2次密码不一样')
                    return
                }
                var obj={
                    uname:this.value1,
                    upwd:this.value2
                }

                this.axios.post("reg",{header:{'Content-Type':'application/x-www-form-urlencoded'}},{params:obj})
                .then(res=>{
                    console.log(res)
                    if(res.data.code==1){
                        alert("注册成功")
                        this.$router.push('/login')   
                }else{
                    alert("注册失败")
                }
                })
                .catch(err=>{
                    console.log(err)
                })
            },
           
            //返回login页面
            gologin(){
                this.$router.push("/login")
            }
        },
        created(){

        },
        watch:{
             //验证账户是否合理
            value1(){
                var reg=/^([A-Za-z0-9]|[._]){3,16}$/
                var v=reg.test(this.value1)
                var d1=document.getElementById("d1")
                if(!v){
                    d1.innerHTML="用户名格式不正确"
                    d1.style.color="red"
                }else{
                    d1.innerHTML="用户名格式正确"
                    d1.style.color="green"
                }
            },
            //验证密码是否合理
            value2(){
                 var reg=/^([A-Za-z0-9]|[._]){6,16}$/
                var v=reg.test(this.value2)
                var d2=document.getElementById("d2")
                if(!v){
                    d2.innerHTML="密码格式不正确"
                    d2.style.color="red"
                }else{
                    d2.innerHTML="密码格式正确"
                    d2.style.color="green"
                }
            },
            value3(){
                 var reg=/^([A-Za-z0-9]|[._]){6,16}$/
                var v=reg.test(this.value3)
                var d3=document.getElementById("d3")
                if(!v){
                    d3.innerHTML="密码格式不正确"
                    d3.style.color="red";
                   
                }else{
                     if(this.value2!=this.value3){
                        d3.innerHTML="2次密码不一样"
                        d3.style.color="yellow"
                    }else{
                    d3.innerHTML="密码格式正确"
                    d3.style.color="green"
                    }
                }
            },
        }
    }
</script>