<template>
    <main>
        <div class="color" v-if="list.length>0">
            <div class="title">{{list[0].section}}:{{list[0].dname}}</div>
            <div>{{list[0].content}}</div>
            <div class="fixed" @click="prev">
                <img src="../assets/叉叉.png" alt="" >
            </div>
        </div>
        <div>
        </div>
    </main>
</template>

<style scoped>
        .title{
            margin:20px}
        .color{
            background:#f3f3f3}
        .fixed{
            position:fixed;
            right:10px;
            top:10px
            }
        
</style>
<script>
import eventBus from '../eventBus.js'
    export default{
        data(){
            return{
                section:"",
                list:[]
            }
        },
        methods:{
            prev(){
                this.$router.push("/")
            },
           //发送请求内容
           getdetails(){
               var obj={
                   id:this.$store.state.id,
                   section:this.section
               }
           this.axios.get("readingdetail",{params:obj})
           .then(res=>{
               this.list=res.data.data;
               console.log(this.list)
           })
           .catch(err=>{
               console.log(err)
           })
           }
              
        },
        created(){
           this.section=this.$route.query.section;
           this.getdetails()
        },    
    }
</script>