<template>
    <main>
        <div class="title">我的书架</div>
        <!--轮播 -->
        <div>
            <div class="carousel">
                <img :src="pic[i]" alt="">
                <div>
                    <a href="javascript:;" @click="changepic(0)" :class="i==0?pics:''"></a>
                    <a href="javascript:;" @click="changepic(1)" :class="i==1?pics:''"></a>
                    <a href="javascript:;" @click="changepic(2)" :class="i==2?pics:''"></a>
                </div>
                <button id="btn1" class="el-icon-caret-left" @click="carousel(-1)"></button>
                <button id="btn2" class="el-icon-caret-right" @click="carousel(+1)"></button>
            </div>
            
        </div>
        <!--轮播 -->
        <div class="flex wrap" v-show="this.$store.state.user!=='用户登录'">
            
            <div v-for="(t,i) of books" :key="i" class="book flex re" >
                <img :src="t.img" alt="" :data-id='t.sid' @click='booknext'>
                <b>{{t.title}}</b>
                <div class="ab">
                <img src="../assets/叉叉.png" alt="" @click="cancel" :data-sid="t.sid" >
                </div>
            </div>
            
            <div  class="book flex">
                <img src="../assets/加号.png" alt="" @click="booklist">
                <b>逛书城</b>
            </div>
        </div>
    </main>
</template>

<style scoped>
    #btn1,#btn2{
        position: absolute;
        background:transparent;
        border:0;
        border-radius: 5px
        }
    #btn1{
        left:0;
        top:50%
        }
    #btn2{
        right:0;
        top:50%
        }
    .carousel{
        position:relative;
        }
    .carousel>div{
        position:absolute;
        bottom:3px;
        left:40%
        }
    .pics{
        background: rgb(243, 227, 216) !important
        }
    .carousel>img{
        width:100%;
        height:150px
        }
    a{
        display:inline-block;
        width:15px;
        height:15px;
        border-radius: 15px;
    background:#f5f5f5;margin-left:3px;}
    .re{
        position: relative;}
    .ab{
        position:absolute;
        top:0;
        right:0;
        }
    .flex{
        display:flex;
        justify-content: flex-start;
        }
    .book{
        width:80px;
        height:120px;
        margin:10px;
        border-radius: 5px;
        background-color:honeydew;
    flex-direction: column}
    .book>img{
        width:100%;
        height:80%;
        border-radius: 5px;}
    .title{
        width:100%;
        height:20%;
        margin:10px;
        border:1px solid #f5f5f5}
    .wrap{
        flex-wrap: wrap !important}
</style>
<script>
    export default{
        data(){
            return{
                books:[],
                pic:[require('../assets/lb01.jpg'),require('../assets/lb02.jpg'),require('../assets/lb03.jpg')],
                i:0,
                pics:{
                    pics:true
                }
            }
        },
        methods:{
            changepic(n){
                this.i=n
            },
            //轮播
            carousel(n){
                this.i+=n;
                if(this.i>2){
                    this.i=0
                };
                if(this.i<0){
                    this.i=2
                }
            },
            booklist(){
                this.$router.push("/books")
            },
            getbookshelf(){
                this.axios.get("getshelf")
                .then(res=>{
                //    sessionStorage.setItem('books',JSON.stringify(res.data.data))
                //    this.books=JSON.parse(sessionStorage.getItem('books'))
                this.books=res.data.data
                    console.log(this.books)
                })
                .catch(err=>{console.log(err)})
            },
            booknext(e){
                var id=e.target.dataset.id;
                this.$router.push("/booknext?id="+id)
            },
            //取消收藏
            cancel(e){
                var sid=e.target.dataset.sid;
                var obj={
                    sid:sid
                }
                this.axios.get("cancel",{params:obj})
                .then(res=>{
                    this.getbookshelf()
                })
                .catch((err)=>{console.log(err)})
            }
        },
        created(){
            setInterval(() => {
                this.carousel(+1)
            }, 3000);
            
            this.getbookshelf()
        },
       
    }
</script>