import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  //保存数据
  state: {
    
    title:"",
    content:"",
    //获取书籍内容，该书的id
    id:1,
    ///****************购物车相关 */
    //保存购物车的数量
    cartcount:0,
    //保存用户信息
    user:"用户登录"
  },
  //获取数据的方法
  getters:{
    setid(state){
      return state.id
    }
    //******************************************* */
  },
  //修改数据的方法
  mutations: {
    
  
    changeid(state,id){
      state.id=id
    },
    changetitle(state,title){
      state.title=title
    },
    changecontent(state,content){
      state.content=content
    },
    ////************************* */
    //购物车数量清空
    clearcart(state){
      state.cartcount=0
    }
  },
  //异步操作
  actions: {
    
  },
  modules: {
   
  }
})
