import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
//引入qs模板，将对象转为字符串格式--发送post请求时使用
import qs from 'qs'
import vuejsonp from 'vue-jsonp'
import 'lib-flexible/flexible.js'
Vue.use(vuejsonp)

//引入axios
import axios from 'axios'
//设置基础路径
axios.defaults.baseURL="http://49.233.215.18:5100/"
axios.defaults.withCredentials=true

//注册axios
Vue.prototype.axios=axios
import Vuex from 'vuex'
Vue.use(Vuex)
//引入mint-ui
import MintUi from 'mint-ui'
import "mint-ui/lib/style.css"
//引入element-ui
import Element from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
Vue.use(Element)
//引入vant-ui
import vant from 'vant'
import 'vant/lib/index.css'
Vue.use(vant)
//虚拟键盘
import vuetouchkeyboard from 'vue-touch-keyboard'
import 'vue-touch-keyboard/dist/vue-touch-keyboard.css'
Vue.use(vuetouchkeyboard)
//注册组件给Vue实列
Vue.use(MintUi)
import elementui from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(elementui)


//事件戳过滤器
Vue.filter("ftime",function(oldval){
  var times;
  var val=parseInt(oldval)
  var year=new Date(val).getFullYear()
  var mouth=new Date(val).getMonth()+1
  var day=new Date(val).getDate()
  times=`${year}-${mouth}-${day}`;
  return times
})
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
